


















// @ is an alias to /src
import { Component } from 'vue-property-decorator'
import Vue from 'vue'
import PortfolioCardComponent from '../components/PortfolioCard.vue'
import ResumeCardComponent from '../components/ResumeCard.vue'
import ProfileCardComponent from '../components/ProfileCard.vue'

@Component({
  components: {
    ProfileCard: ProfileCardComponent,
    PortfolioCard: PortfolioCardComponent,
    ResumeCard: ResumeCardComponent
  },
  name: 'home'
})
export default class HomePage extends Vue {

  goTo(path: string): void {
    console.log('path', path)
    this.$router.push(path)
  }
}
