







































import { Component, Vue } from 'vue-property-decorator'
import { version } from '../package.json'
@Component
export default class App extends Vue {
  constructor() {
    super()
  }

  home() {
    this.$router.replace({ path: '/', replace: true })
  }

  get year(): number {
    return new Date().getFullYear()
  }

  get version(): string {
    return version
  }
}
